.thansk{
	
	min-height: 300px;
	height: 100vh;
	position: relative;
	& > *{
		position: relative;
		z-index: 2;
	}
	&:before {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		background: url(../img/thanks.jpg) no-repeat 30% 40%;
		background-size: cover;
	}
	.center{
		height: 100%;
		.flex-container{
			height: 100%;
			flex-direction: column;
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			justify-content: center;
	}
}
}




.thanks_wrapper {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	h2{
	font-size: 30px;
	padding-top: 30px;
	display: block;
	margin-bottom: 20px;
	font-weight: 500;
	text-transform: none;

	}
}


 .thanks_btn {
 	text-transform: uppercase;
        cursor:pointer;
            background: #009eff;
            transition: all 0.3s linear;
            &:hover {
    			box-shadow: 1px 9px 29px 8px rgba(0,0,0,0.4);
            }
            margin-top: 20px;
            width: 38%;
            min-width: 268px;
            padding: 20px 73px;
            border-radius: 35px;
            color: @white;

    }

  


@media screen and (max-width: 690px) {
	.thanks_wrapper {
		max-width: 580px;
	}
}

@media screen and (max-width: 550px) {
	.thanks_btn{
		margin: 80px auto 0;
	}

	.thanks_wrapper{
		background: transparent !important;
	}
}

