
.only_form {
    background: #f2f2f2;
    .flex-container {
        flex-direction: column;
        padding: 80px 0;
    }

    .only_form-title {
        margin-bottom: 40px;
    }
}


.numbers-form-wrapper {
    max-width:  500px;
    width: 100%;
    min-width: 275px;
    padding: 30px 40px;
    text-align: center;
    position: relative;
    margin: auto;
    vertical-align: middle;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #fff;
    border-radius: 5px;
    h2 {
        color: @black;
        text-transform: capitalize;
    }
    span {
        color: @black;
    }
}

.numbers-form-wrapper .text-field {
    margin-bottom: 30px;
}


.numbers-form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    width: 100%;
    span {
        align-self: flex-start;
        margin-top: 20px;
    }
}


.input-wrapper {
    position: relative;
        width: 100%;
        height: 52px;
        top: 0;
        left: 0;
       
        & > input {
            position: absolute;
            top: 1px;
            left: 1px;
            padding: 25px 30px;
            width: 99.5%;
            font-size: 16px;
            height: 95%;
        }
    }


    .numbers_btn {
        cursor:pointer;
            background: #f5d600;
            text-transform: uppercase;
            transition: all 0.3s linear;
             
            &:hover {
             box-shadow: 1px 9px 29px 8px rgba(0,0,0,0.4);
            
            }
            margin-top: 35px;
            width: 100%;
            padding: 20px 35px;
            border-radius: 35px;
            color: @black;

    }


    .intl-tel-input.allow-dropdown input, .intl-tel-input.allow-dropdown input[type=text], .intl-tel-input.allow-dropdown input[type=tel], .intl-tel-input.separate-dial-code input, .intl-tel-input.separate-dial-code input[type=text], .intl-tel-input.separate-dial-code input[type=tel] {
       
    margin-left: 0;
    width: 100% !important;
        padding: 17px 6px 15px 74px;
        margin-top: 1px !important;

    }


    .intl-tel-input.allow-dropdown {
        width: 99.5%;
        height: 99%;
    }


    .intl-tel-input .selected-flag {
        padding: 0 6px 0 35px;
    }





#telephone {
    font-size: 16px;
}



