

@media screen and (max-width: 830px) {

}





@media screen and(max-width: 580px) {
	body {
		font-size: 12px;
		
	}

	.main_section {
	background: url(../img/bg_mob.png) no-repeat center;
		background-size: cover;
}
	h2 {
		font-size: 24px;
	}
	.wrap_content {
		padding-bottom: 40px;
	}
	.top_content {
		margin: 0 auto;
		h1, p{
			font-size: 18px;
			font-weight: @regular;
		}
		h1 > span {
			font-size: 36px;
			font-weight: @regular;
			line-height: 25px;
			display: block;
			margin: 15px 0;

		}
	}

	.sum_cards-wrap {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		.sum_cards{
			p{
				font-size: 27px;
			}
			&:first-child{
				align-self: flex-start;
			}
			&:nth-child(2){
				align-self: flex-end;
				margin-top: 18px;
			}
			&:last-child {
				align-self: flex-start;
				margin-top: 18px;
				margin-left: 15px;
			}
		}
	}

	.content_button {
		margin-top: 68px;
		a {
			span {
				font-weight: @bold;
				font-size: 14px;
			}
		}
	}

	.numbers_btn {
		font-weight: @bold;
		padding: 20px 10px;
	}



.thansk{
	&:before {
		background: url(../img/thx_bg_m.jpg) no-repeat center;
		background-size: cover;
	}
	.center{
		.flex-container{
			align-items: center;
			justify-content: center;
			.thanks_btn{
				margin-top: auto;
				font-size: 12px;
				padding: 20px 88px;
				display: block;
				margin: 0 auto;
			}
			h2{
				font-size: 24px;
				font-weight: 500;
				margin-bottom: 10px;
				line-height: 23px;
				line-height: 30px;
				br{
					display: none;
				}
			}
	}
}
}

.thanks_wrapper {
		justify-content: space-between;
		height: 100%;
		padding: 28% 0 5% 0;
}
}


