@import "node_modules/normalize.less/normalize.less";
@import "_variables.less";
@import "_timer.less";
@import "_thanks.less";

*, *:after, *:before {
	box-sizing: border-box;
}
html, body{
	overflow-x: hidden;
}

body {
	font-family: @font_;
	font-size: 16px;
	font-weight: @regular;
	overflow-x: hidden;
	
	
}


ul, li {
	list-style: none;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
	color: @black;
}

img {
	display: block;
	max-width: 100%;
}

h1, h2, h3, h4, h5, h6, p {
	padding: 0;
	margin: 0;
}

h2 {
	text-transform: uppercase;
	font-size: 30px;
	font-size: @bold;
}

input {
	outline: none;
}

input[type=submit] {
	border: none;
}

a, input[class$="_btn"] {
	font-size: 16px;
}

.main_section {
	background: url(../img/bg-min.jpg) no-repeat center;
	background-size: cover;
}

.center {
	max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
}

.flex-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
}

.wrap_content {
	display: flex;
	justify-content: center;
	align-items: flex-start;
	flex-wrap: wrap;
	width: 100%;
	flex-direction: column;
	padding-top: 8em;
	h1, p {
		color: @white;
	}
}

.hidden() {
	display: none;
}

.mt() {
	margin-top:120px;
}

	

	body, .main_section, .center, .flex-container {
		height: 100vh;
		

	}
	



	.content_button {
		margin-top: 128px;
		a {
			padding: 23px 35px;
			border-radius: 35px;
background: #f5d600;
			transition: all 0.3s linear;
			&:hover {
			box-shadow: 1px 9px 29px 8px rgba(0,0,0,0.4);
			}
			span {
				color: @black;
				vertical-align: middle;
			}
		}
	}

.top_content {
	h1 {
		font-size: 27px;
		font-weight: 500;
		text-shadow: 0 1px 3px rgba(0, 0, 0, 0.19);
		span {
			font-size: 57px;
			text-shadow: 0 1px 3px rgba(0, 0, 0, 0.19);
		}

	}

	p{
		font-size: 27px;
		font-weight: 500;
		text-shadow: 0 1px 3px rgba(0, 0, 0, 0.19);
		margin-top: 5px !important;
	}
	.sum_title {
		margin-top: 55px;
		color: @white;
		text-shadow: 0 1px 3px rgba(0, 0, 0, 0.19);
	}


	.sum_cards-wrap {
		margin-top: 13px;
    	display: flex;
    	justify-content: space-between;
    	align-items: center;
    	flex-wrap: wrap;
    	max-width: 573px;

    	.sum_cards {
    		position: relative;
    		display: inline-flex;
    		align-items: center;
    		justify-content: center;
    		padding: 8px 30px;
    		border-radius: 10px;
    		min-width: 170px;
    		width: 170px;
    		height: 52px;
    		z-index: 5;
    		&:before {
    			content: "";
    			position: absolute;
    			width: 10px;
    			height: 10px;
    			background: #49b4bd;
    			border-radius: 50%;
    			top: 29px;
    			left: 10px;
    			z-index: 2;
    		}

    		&:after {
    			content: "";
    			position: absolute;
    			width: 100%;
    			height: 100%;
    			background: #fff;
    			border-radius: 10px;
    			// top: 40px;
    			// left: 2.5px;
    			border-radius: 78px;
    			z-index: 1;

    		}
    		
    		background: #fff;
    		p{
    			color: @black;
    			font-weight: 700;
    			position: relative;
    			z-index: 5;
    		}
    		img {
    			margin-right: 12px;
    			position: relative;
    			z-index: 5;
    		}
    	}
    }
}


.bottom_content {
	margin: 0 auto;
	    max-width: 370px;
    text-align: center;
    p {
    	    margin-top: 60px;
    }
}

.effect {
	position: absolute;
    width: 48%;
    height: 0%;
    box-shadow: 76px -7px 13px 2px rgba(0,0,0,.95);
    transform: rotate(7deg);
    top: 49px;
    left: 0;
    z-index: -20;
}






// Modal window
#modal_form {
	width: 100%; 
	//height: 380px; /* Рaзмеры дoлжны быть фиксирoвaны */
	border-radius: 5px;
	position: fixed; /* чтoбы oкнo былo в видимoй зoне в любoм месте */
	//top: 45%; /* oтступaем сверху 45%, oстaльные 5% пoдвинет скрипт */
	//left: 50%; /* пoлoвинa экрaнa слевa */
	// margin-top: -150px;
	// margin-left: -150px; /* тут вся мaгия центрoвки css, oтступaем влевo и вверх минус пoлoвину ширины и высoты сooтветственнo =) */
	display: none; /* в oбычнoм сoстoянии oкнa не дoлжнo быть */
	opacity: 0; /* пoлнoстью прoзрaчнo для aнимирoвaния */
	z-index: 35; /* oкнo дoлжнo быть нaибoлее бoльшем слoе */
	padding: 20px 10px;
	margin: 0 auto;
}
/* Кнoпкa зaкрыть для тех ктo в тaнке) */
#modal_form #modal_close {
	
	top: 10px;
	right: 10px;
	cursor: pointer;
	display: block;
}
/* Пoдлoжкa */
#overlay {
	z-index:30; /* пoдлoжкa дoлжнa быть выше слoев элементoв сaйтa, нo ниже слoя мoдaльнoгo oкнa */
	position:fixed; /* всегдa перекрывaет весь сaйт */
	background-color:#000; /* чернaя */
	opacity:0.9; /* нo немнoгo прoзрaчнa */
	-moz-opacity:0.9; /* фикс прозрачности для старых браузеров */
	filter:alpha(opacity=90);
	width:100%; 
	height:100%; /* рaзмерoм вo весь экрaн */
	top:0; /* сверху и слевa 0, oбязaтельные свoйствa! */
	left:0;
	cursor:pointer;
	display:none; /* в oбычнoм сoстoянии её нет) */
}


.open_modal {
	width: 100%;
	text-transform: uppercase;
	font-weight: 500;
	box-shadow: 0 5px 23px 4px rgba(0, 0, 0, 0.17);
	margin-bottom: 42px !important;
}


.modal_content {
	// color: @white;
	// display: flex;
	// align-items: center;
	// justify-content: center;
	// flex-wrap: wrap;
	// height: 100%;
	// text-align: center;
	h2 {
		vertical-align: middle;
		text-transform: none;
	}
}


#modal_close {
	background: @white;
	position: absolute;
	width: 25px;
	height: 2px;
	 float: right;
	transform: rotate(45deg);
	transition: all 0.05s linear;
	// &:hover {
	// 	transform: rotate(0deg);
	// }


}

#modal_close:before {
	position: absolute;
	content: "";
	background: @white;
	width: 25px;
	height: 2px;
	transform: rotate(-90deg);
	width: 25px;
	height: 2px;
	transition: all 0.05s linear;
	// &:hover {
	// 	display: none;
	// }
}

.wrap_close{
    display: none;
    cursor: pointer;
    position: absolute;
    width: 10%;
    float: right;
    height: 30px;
    -webkit-box-pack: end;
    -webkit-justify-content: flex-end;
    -moz-box-pack: end;
    -ms-flex-pack: end;
    justify-content: flex-end;
    top: 0;
    right: 5%;
		&:hover {
			#modal_close:before {
				transform: rotate(0deg);
			}
			#modal_close{
				transform: rotate(0deg);
			}
		}
}












